import Vue from 'vue'
import Vuex from 'vuex'
import { 
    cacheState, 
    compile, 
    downloadProject, 
    downloadScript,
    loadProjectFromFile,
    loadProjectFromCache,
} from './transfer'
import { 
    createResource, 
    createRole, 
    createChunk, 
    removeResource, 
    removeRole, 
    removeChunk, 
    updateData, 
} from './data-mutation'
import { v4 as genId } from 'uuid';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    id: genId(),

    name: '', // 
    
    version: '1.0.0', // 

    authors: [], // 

    data: {}, // 

    resources: [], // 

    roles: [], // 

    chunks: [], // 

    script: null, // 

  },
  mutations: {
    cacheState,
    compile,
    downloadProject,
    downloadScript,
    loadProjectFromFile,
    loadProjectFromCache,


    createResource,
    createRole,
    createChunk,

    removeResource,
    removeRole,
    removeChunk,

    updateData,

    updateBaseInfo(state, { name, version, authors }) {
      state.name = name || state.name;
      state.version = version || state.version;
      state.authors = authors || state.authors;
    },

  },
  actions: {
  },
})
